@font-face {
  font-display: swap;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  src: url("inter-v18-latin-regular.4868384e.woff2") format("woff2");
}

@font-face {
  font-display: swap;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  src: url("inter-v18-latin-500.bb666cf6.woff2") format("woff2");
}

@font-face {
  font-display: swap;
  font-family: Bluu Next Cyrillic;
  font-style: normal;
  font-weight: 700;
  src: url("Bluu-Next-Cyrillic.e442c015.woff2") format("woff2");
}

:root {
  --color-primary-dark: #1d1d1d;
  --color-primary-light: #fabf59;
  --color-accent-light: #fff;
  --color-background-light: #d9d9d9;
  --color-secondary: #f9b50c;
}

body {
  background-color: var(--color-primary-dark);
  color: var(--color-primary-light);
  margin: 0;
  font-family: Inter, sans-serif;
}

.container {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1288px;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

img {
  -o-object-fit: cover;
  object-fit: cover;
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  top: auto;
  left: -10000px;
  overflow: hidden;
}

.hidden {
  display: none;
}

.characters-cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.characters-cards__card {
  background-color: var(--color-primary);
  text-align: center;
  border-radius: 12px;
  width: 200px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px #00000080;
}

.characters-cards__card:hover {
  transform: scale(1.05);
}

.characters-cards__image {
  width: 100%;
  height: auto;
}

.characters-cards__info {
  color: var(--color-secondary);
  padding: 10px;
}

.hero {
  text-align: center;
  background-image: url("hero.622d0288.jpg");
  background-position: center;
  background-size: cover;
  padding: 120px 0;
}

@media screen and (min-device-pixel-ratio: 2), (-webkit-device-pixel-ratio >= 2), (resolution >= 192dpi), (resolution >= 2x) {
  .hero {
    background-image: url("hero@2x.e75b92d2.jpg");
  }
}

.hero__title {
  margin-bottom: 30px;
  font-family: Bluu Next Cyrillic, sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 1.2;
}

.hero__btn {
  background-color: var(--color-secondary);
  color: var(--color-primary-dark);
  border: none;
  border-radius: 100px;
  align-items: center;
  padding: 18px 36px;
  font-family: Bluu Next Cyrillic, sans-serif;
  font-size: 18px;
  transition: transform .3s, background-color .3s;
}

.hero__btn:hover {
  background-color: var(--color-primary-light);
  transform: scale(1.05);
}

.characters {
  background-color: var(--color-primary-dark);
  padding-block: 60px;
}

.characters__container {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.characters__list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.characters__item {
  text-align: center;
  max-width: 400px;
}

.characters__image {
  border-radius: 20px;
  width: 100%;
  transition: transform .3s;
  box-shadow: 0 4px 12px #ffffff1a;
}

.characters__image:hover {
  transform: scale(1.05);
}

.characters__btn {
  border: 1px solid var(--color-secondary);
  color: var(--color-secondary);
  background: none;
  border-radius: 100px;
  margin-top: 20px;
  padding: 18px 36px;
  font-family: Bluu Next Cyrillic, sans-serif;
  font-size: 18px;
  transition: color .3s, background-color .3s, transform .3s;
}

.characters__btn:hover {
  background-color: var(--color-secondary);
  color: var(--color-primary-dark);
  transform: scale(1.05);
}
